body {
  background-color: #1B1B1E;
  color: #D8DBE2;
  margin: 0;
  padding: 0;
}

.container {
  width: 100%;
  display: grid;
  grid-template: 40px auto 40px/ repeat(12, 1fr);
  grid-template-areas:
    "h h h h h h h h h h h h"
    "m m c c c c c c c c c c"
    "f f f f f f f f f f f f";
    max-width: 100%;
    margin: auto;
}

.my-profile-photo {
  width: 30%;
}

.navBar {
  background-color: #133C55;
  grid-area: h;
}

.menu {
  grid-area: m;
}

.container-article{
  grid-area: c;
}

.footer {
  background-color: #133C55;
  display: inline;
  grid-area: f;
  height: 100%;
}

ul {
  display: inline-block;
}
